                /* eslint-disable indent  */
                switch (type) {
                    case 'index':
                        this.$router.push({
                            name: 'feedback-index'
                        })
                        break
                    case 'add':
                        this.$router.push({
                            name: 'feedback-add'
                        })
                        break
                    default:
